.header {
  display: block;
  width: 100%;
  height: 70px;
  box-shadow: 0 2px 9px rgba(189, 189, 189, 0.5);
  background-color: #ffffff;
  &__logo-container {
    display: block;
    float: left;
    width: 21px;
    height: 31px;
    margin-left: 39px;
    margin-top: 20px;
  }
  &__logo {
    display: block;
    text-decoration: none;
  }
  &__checkbox {
    display: none;
  }
  &__burger {
    display: none;
  }
  &__menu {
    display: block;
    float: left;
    margin-top: 25px;

  }
  &__menu-item {
    display: inline-block;
    text-transform: uppercase;
    padding-left: 57px;
    &:first-child {
      padding-left: 29px;
    }
    a {
      display: block;
      text-decoration: none;
      color: #9297a3;
      font-family: "PT Sans - Narrow Bold", sans-serif;
      font-size: 11px;
      line-height: 25px;
      font-weight: 700;
    }
    a:hover {
      transition: 0.6s;
      color: black;
    }
  }

  &__autorization {
    display: block;
    float: right;

  }
  &__button {
    margin-right: 36px;
    background-color: transparent;
    outline: none;
    color: #333743;
    font-family: "PT Sans - Narrow Bold", sans-serif;
    font-size: 13px;
    font-weight: 700;
    line-height: 20px;
    border: 2px solid transparent;
    &:hover {
      transition: 0.8s;
    }
    &:first-child {
      border-right: 1px solid #dedede;
      height: 69px;
      padding-right: 57px;
      &:hover {
        color: #e51b1b;
      }
    }
    &:first-child:after {
      content: "3";
      margin-left: 13px;
      padding: 1px 5px;
      border: 1px solid #333743;
      border-radius: 50%;
    }
    &:nth-child(2):hover {
      color: #227d80;
      border-color: #45b940;
    }
    &:last-child {
      opacity: 0.7;
      &:hover {
        color: #429297;
        border-color: #52cfd4;
      }
    }
  }
  @media all and (max-width: 886px) {
    &__logo-container {
      margin-left: 19px;
    }
    &__menu {
      padding-left: 0;
    }
    &__menu-item {
      padding-left: 20px;
    }
    &__button {
      margin-right: 10px;
    }
    &__button:first-child {
      padding-right: 20px;
    }
  }
  @media all and (max-width: 680px) {
    height: 50px;
    &__logo-container {
      margin-top: 10px;
    }
    &__menu {
      width: 100%;
      //height: 100px;
      //position: absolute;
      top: 50px;
      left: 0;
      padding-left: 0;
      margin-top: 10px;
      margin-bottom: 0;
      z-index: 100;
      display: none;
    }
    &__menu-item {
      display: block;
      text-align: center;
      padding: 5px 0;
      border: 1px solid #c4c4c4;
      border-top: none;
      &:hover {
        background-color: rgba(154, 205, 50, 0.63);
        transition: 0.6s;
      }
      &:hover a {
        color: #333743;
      }
      &:first-child {
        padding-left: 0;
      }
    }
    &__autorization {
      position: absolute;
      right: 10px;
    }
    &__button:first-child {
      height: 50px;
      padding-right: 10px;
    }
    &__button:last-child {
      margin-right: 60px;
    }
    &__burger {
      display: block;
      margin-top: 15px;
      width: 24px;
      height: 24px;
      cursor: pointer;
      color: transparent;
      font-size: 0;
      background: url(./../assets/menu.png) 100%, no-repeat;
      position: absolute;
      right: 20px;
      z-index: 1;
    }
    &__checkbox {
      display: none;

      &:checked {

        + .header__burger {
          background-image: url(./../assets/multiply.png);
        }

        ~ .header__menu {
          display: block;
        }
      }
    }
  }
  @media all and (max-width: 330px) {
    &__logo-container {
      margin-left: 15px;
    }
    &__button:first-child {
      padding-right: 10px;
    }
  }
}